export default {
  layout: "Dum-content",
  mounted() {
    this.getprogram();
  },
  data() {
    return {
      programs: [],
      program: {
        name: "",
        duration: "",
        new_image: "",

      },
      errors: {},
      base_url: process.env.url
    };
  },
  methods: {
    getprogram() {
      this.$axios
        .$get("/program/show")
        .then((res) => {
          this.programs = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    programEdit(id) {
      $("#programUpdate").modal("show");
      this.$axios
        .$get("/program/edit/" + id)
        .then((res) => {
          this.program = res;

        })
        .catch((err) => {
          console.log(err);
        });

    },
    programUpdate() {
      let formData = new FormData();
      formData.append('name', this.program.name)
      formData.append('duration', this.program.duration)
      if (this.program.new_image) {
        formData.append('image', this.program.new_image)
      }
      this.$axios
        .$post("/program/update/" + this.program.id, formData)
        .then((res) => {
          this.getprogram();
          $("#programUpdate").modal("hide");
          this.$toaster.success(res.message);
          this.errors = "";
        })
        .catch((err) => {
          console.log(err);
          this.errors = err.response.data.errors;
        });
    },
    deleteprogram(id) {
      if (confirm("Are you sure to delete this program?")) {
        this.$axios
          .$get("/program/delete/" + id)
          .then((res) => {
            this.getprogram();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }

    },
    programStatus(id, status) {
      this.$axios
        .$get("/program/status/" + id)
        .then((res) => {
          this.getprogram();
          this.$toaster.success(res.message);
        })
        .catch((err) => {
          console.log(err);
        });
    },









  }
};
